import NavBar from '../components/NavBar/NavBar';
import PostGrid from '../components/Posts/PostGrid';
import {makeCMSContentRequest} from '../services/CMSrequest';
import React from 'react';
import styled from '@emotion/styled';
import { url } from 'inspector';

const Index = (data: any) => {
  return(
    <>
      <NavBar/>
        <HomePageContainer>
          <PostGrid posts = {data}/>
        </HomePageContainer>
    </>
  )
}

Index.getInitialProps = async() => {
  const data = await makeCMSContentRequest('blogPost');
  return {data};
}

const HomePageContainer=styled.div`
  background: url(https://www.textures.com/system/gallery/photos/Wood/Planks/Clean/3395/WoodPlanksClean0025_11_download600.jpg) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  
  `

export default Index
